body {
  margin: 0;
  background-color: #eee;
  max-width: 400px;
  max-height: 700px;
  min-height: 700px;
}

#root {
  width: 100%;
  height: 100%;
}

.white-box {
  color: black;
  border: 1px solid black;
  background-color: white;
}

div {
  box-sizing: border-box;
  color: rgb(45, 45, 45);
  padding: 1.25%;
}

.sort {
  display: flex;
  align-items: center;
  justify-content: center;
}

.App {
  border-radius: 20px;
  padding: 0px;
  width: 100%;
  height: 100%;
  min-height: 700px;
  background-color: white;
  color: rgb(236, 48, 27);
}
.App p {
  transform: rotate(0.03deg);
}

.setting {
  margin-top: 60px;
  height: 180px;
  width: 100%;
  background-color: rgb(208, 229, 243);
  border-top: 3px solid rgb(98, 188, 248);
  color: white;
}
.setting .half {
  width: 50%;
  height: 100%;
}
.setting .half input {
  background-color: rgb(208, 229, 243);
  border: 0;
  border-bottom: solid 3px rgb(98, 188, 248);
}
.setting .pu {
  height: 36%;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.col-2 {
  width: 16.6666666667%;
}

.main {
  height: auto;
}
.main p {
  margin: 0px;
}
.main .bb {
  padding: 0px;
  margin: 0px;
  border-bottom: 2px dashed black;
}
.main .title {
  height: 55px;
}
.main .right {
  display: flex;
  justify-content: right;
}

.main-nav1 {
  z-index: 100;
  position: absolute;
  top: 1.5%;
  left: 2.5%;
  width: 15%;
  background-color: rgb(98, 188, 248);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 7.5%;
  border-radius: 10px;
  transition: all 1s;
  border: solid white 2px;
}
.main-nav1 .cont {
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-nav2 {
  z-index: 100;
  position: absolute;
  top: 1.5%;
  left: 2.5%;
  width: 15%;
  background-color: white;
  height: 45%;
  border-radius: 20px;
  border: solid rgb(3, 150, 248) 2px;
  transition: all 1s;
}
.main-nav2 .cont {
  height: 17.5%;
  color: rgb(3, 150, 248);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 8vw;
}

.bo {
  border: 2px;
}

.red {
  color: red;
}

.nav {
  border-radius: 20px;
  height: 20%;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  background: linear-gradient(to right, rgb(83, 185, 253), rgb(3, 150, 248));
  color: white;
  padding: 3%;
}
.nav div {
  font-size: 2.3vh;
  font-weight: 500;
  padding: 0px;
  color: white;
  text-align: right;
}

.btn-box {
  margin-top: 15px;
  height: 80px;
  padding: 10px;
  background-color: rgb(208, 229, 243);
  color: white;
  display: flex;
  align-items: center;
  justify-content: right;
}
.btn-box .btn1 {
  transition: all 0.5s;
  width: 65px;
  height: 40px;
  background-color: rgb(83, 185, 253);
  color: white;
  border: 1.5px solid white;
  border-radius: 5px;
  margin-left: 5px;
}
.btn-box .btn2 {
  transition: all 0.5s;
  width: 70px;
  height: 45px;
  background-color: white;
  color: rgb(59, 157, 243);
  font-size: 4.5vw;
  font-weight: 700;
  border: 3px solid rgb(59, 157, 243);
  border-radius: 5px;
  margin-left: 5px;
}

.ball {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  color: white;
  background-color: rgb(3, 150, 248);
  font-weight: 700;
}

.long-box {
  background-color: white;
  border-radius: 40px;
  margin-bottom: 2%;
}

.boxbox {
  background-color: rgb(208, 229, 243);
}

.expectmain {
  transition: all 1s;
  margin-top: 15px;
  background-color: white;
  text-align: center;
}
.expectmain .expect {
  transition: all 1s;
  color: black;
  font-size: 1rem;
}
.expectmain .bord {
  border-radius: 20px;
  background-color: white;
}
.expectmain .inball {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  color: white;
  font-weight: 600;
}
.expectmain .text {
  color: black;
  font-weight: 600;
}

.click {
  font-size: 1.5rem;
  width: 60px;
  height: 60px;
  background-color: rgb(208, 229, 243);
  color: white;
  border-radius: 30px;
}

.spin1 {
  transition: all 1s;
  transform: rotateX(180deg);
}

.spin2 {
  transition: all 1s;
}

.inex1 {
  margin-top: 10px;
  background-color: rgb(208, 229, 243);
  color: white;
}

.main-last {
  margin-top: 15px;
  background-color: white;
  border: 10px solid rgb(208, 229, 243);
}
.main-last .text {
  color: black;
  font-size: 1rem;
  text-align: left;
  border-bottom: 1.5px solid rgb(59, 157, 243);
}

.custom {
  background-color: rgb(223, 237, 246);
}
.custom .buttonon {
  transition: all 0.5s;
  width: 20%;
  height: 110%;
  margin: 2.5%;
  border: 2px white solid;
  background-color: rgb(59, 157, 243);
  color: white;
  border-radius: 10px;
}
.custom .buttondown {
  transition: all 0.5s;
  width: 20%;
  height: 100%;
  margin: 2.5%;
  border: 2px rgb(59, 157, 243) solid;
  background-color: white;
  color: black;
  border-radius: 10px;
}

.exp {
  margin-top: 5px;
  background-color: white;
  border: 10px solid rgb(223, 237, 246);
}
.exp div {
  font-size: 700;
  color: black;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.expmain .exback {
  background-color: rgb(223, 237, 246);
}
.expmain .exbtn1 {
  transition: all 0.3s;
  width: 17.6%;
  margin: 1%;
  background-color: white;
  border-radius: 5px;
  border: 2px solid rgb(59, 157, 243);
  display: flex;
  align-items: center;
  justify-content: center;
}
.expmain .exbtn2 {
  transition: all 0.3s;
  width: 17.5%;
  font-size: 1.1rem;
  width: 17.5%;
  margin: 1%;
  background-color: rgb(59, 157, 243);
  border-radius: 5px;
  border: 2px solid white;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.expmain .expectsub {
  margin-top: 10px;
  height: auto;
  background-color: rgb(197, 224, 241);
}
.expmain .expectsub div {
  color: black;
}
.expmain .aa {
  background-color: white;
}
.expmain .expdown {
  transition: all 1s;
  transform: rotateX(180deg);
  background-color: rgb(59, 157, 243);
  width: 30px;
  height: 30px;
  border-radius: 20px;
  color: white;
}
.expmain .expup {
  transition: all 1s;
  background-color: rgb(59, 157, 243);
  width: 30px;
  height: 30px;
  border-radius: 20px;
  color: white;
}/*# sourceMappingURL=App.css.map */